@import url('https://fonts.googleapis.com/css?family=Baloo+Bhai|Karla:400,400i,700|Lobster');
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  line-height:2;
  position:relative;
}
main {
  font-family: 'Karla', sans-serif;
  margin-top: 100px;
}

p {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}
h2 {
  font-size:22px;
}
h3 {
  font-size:20px;
  margin-bottom:5px;
  margin-top:15px;
}
h4 {
  font-size:16px;
}
h5 {
  font-size:15px;
}
.float-right {
  float:right;
}
.float-left {
  float:left;
}
.inline {
  display: inline;
  clear:both;
}
.left {
  margin-right: 15px;
}
.right {
  margin-left:15px;
}
/*
* — Header —
*/
header {
  background-color: #E3E1E3;
  position:fixed;
  top:0;
  width:100%;
  z-index:100;
}
.logo {
  display:block;
  width:35%;
  height:auto;
  margin:0 auto;
  padding:15px;
  max-width:170px;
}
/*
* — Navigation —
*/

.hamburger-icon,
.close-icon {
  z-index: 2;
  position: absolute;
  right: 10px;
  top: 10px;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
  clear:both;
}
nav > ul {
  margin:10px auto;
  list-style: none;
  -webkit-margin-before:0;
  -webkit-padding-start:0;
  -webkit-margin-after: 0;
}
nav li {
  font-family: 'Baloo Bhai', cursive;
  font-size: 1.75rem;
  width: 50%;
  margin: 0;
  height: 75px;
  padding-top: 30px;
  display: inline-block;
}
nav li:first-child {
  background-color:#306030;
}
nav li:nth-child(2) {
  background-color:#d89030;
}
nav li:nth-child(3) {
  background-color:#d87878;
}
nav li:last-child {
  background-color:#483030;
}
nav li:first-child:hover {
  background-color:#193319;
  cursor: pointer;
}
nav li:nth-child(2):hover {
  background-color:#b37522;
  cursor: pointer;
}
nav li:nth-child(3):hover {
  background-color:#c94343;
  cursor: pointer;
}
nav li:last-child:hover {
  background-color:#1f1515;
  cursor: pointer;
}
nav a {
  color:#fff;
  text-decoration: none;
  padding-top:15px;
}
nav a:hover {
  color:#fff;
  text-decoration: none;
  font-weight: bolder;
  cursor: pointer;
}
nav a:active,
nav a:visited {
  color:#FFF;
  text-decoration: none;
}
#home,
#about,
#books,
#reviews {
  width:90%;
  margin-bottom: 50px;
  padding-top: 70px;
  margin-top: -70px;
  margin-left:auto;
  margin-right:auto;
}
/*
* — Home —
*/
#home {
  padding-bottom: 35px;
}
.carousel p {
  margin-bottom: 10px;
  line-height:1.25;
}
.slide {
  border-radius: 10px;
  width: 90%;
  height: 500px;
  margin: auto;
}
.grey-backdrop {
    position: absolute;
    bottom: 35px;
    left: 5%;
    width: 90%;
    background: rgba(0, 0, 0, 0.3);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.caption {
    width: 70%;
    margin:0 auto;
}
.caption h3,
.caption p {
    color:#fff;
    text-shadow: 2px 2px 4px #000000;
}
.green {
  color:#306030;
  font-family: 'Baloo Bhai', cursive;
}
.buttons {
  width:100%;
}
.left-arrow,
.right-arrow {
  width:20px;
  height:auto;
  position: absolute;
  z-index:2;
}

.left-arrow {
  left:25px;
  bottom: 85px;
}
.right-arrow {
  right:25px;
  bottom: 85px;
}
/*
* — About —
*/
#about p {
  margin-top: 25px;
}
.brown {
  color:#604830;
  font-family: 'Baloo Bhai', cursive;
}
.paul-and-jerry {
    width:300px;
    height: auto;
    margin: 10px auto;
    display:block;
    border: #d89030 solid 20px
}
/*
* — Books —
*/
.pink {
  color:#812525;
  font-family: 'Baloo Bhai', cursive;
}
.book-title {
  font-weight:bolder;
}
.book-subtitle {
  -webkit-text-emphasis: emp;
          text-emphasis: emp;
}
.book-covers {
  height:270px;
  margin-bottom:20px;
  margin-top:15px;
}
.cover {
  width:180px;
  height:auto;
  float:left;
  margin-left: 25px;
  -webkit-box-shadow: 0 10px 25px -5px rgba(2, 2, 2, .8);
          box-shadow: 0 10px 25px -5px rgba(2, 2, 2, .8);
}
.buy-now {
  color:#fff;
  text-align:center;
  font-size: 18px;
  width: 35%;
  margin: 25% 10px 0 10px;
  font-family: 'Lobster', cursive;
  background: #5aad5a;
  border: #0B6623 solid 2px;
  border-radius: 10px;
  padding: 2.5px 8px;
  float:right;
}
.buy-now:hover {
  cursor: pointer;
  color:#fff;
  background:#b5dab5;
  text-decoration: none;
}
.buy-now:active,
.buy-now:visited {
  color:#FFF;
  text-decoration: none;
}
.synopsis {
  margin-top: 45px;
}
.quote-snippets {
  margin-top: 35px;
}
.quote-books {
  line-height:1.75;
  display:inline;
}
.quote-marks {
  width:24px;
  display: inline;
}
.for-books {
  -webkit-filter: invert(15%) sepia(39%) saturate(5702%) hue-rotate(350deg) brightness(92%) contrast(127%);
          filter: invert(15%) sepia(39%) saturate(5702%) hue-rotate(350deg) brightness(92%) contrast(127%);
}
.quote-source {
  color:#900;
  margin-bottom:15px;
}
/*
* — Reviews —
*/
.purple {
  color:#483030;
  font-family: 'Baloo Bhai', cursive;
}
#reviews {
  min-height: 600px;
}

.sammy-cover {
  width:35%;
  max-width:175px;
  height:auto;
  margin: 20px auto 25px;
  display: block;
  -webkit-transform: rotate(-5deg);
      -ms-transform: rotate(-5deg);
          transform: rotate(-5deg);
}
.quotes {
  margin-top: 50px;
}
.for-reviews {
  -webkit-filter: invert(16%) sepia(14%) saturate(1215%) hue-rotate(314deg) brightness(103%) contrast(88%);
          filter: invert(16%) sepia(14%) saturate(1215%) hue-rotate(314deg) brightness(103%) contrast(88%);
}
.quote {
  font-size:16px;
  font-style:italic;
  color:#353535;
  line-height:22px;
  margin-bottom: 10px;
  display:inline;
}
.quote-appear {
  opacity: 0.01;
}

.quote-appear.quote-appear-active {
  opacity: 1;
  -webkit-transition: opacity .5s ease-in;
  -o-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}
.cite {
  font-size: 12px;
  font-weight: bolder;
  color: #483030;;
  -webkit-margin-after: 0;
  margin-bottom:4px;
}
/*
* — Footer —
*/
footer {
  background-color: #E3E1E3;
  padding-top: 70px;
  margin-top: 70px;
  width:100%;
  min-height: 200px;
  position: absolute;
}
.logo-footer {
  width: 20%;
  display: block;
  margin: -40px auto 0;
  max-width:130px;
}
.social-icons {
  width: 70%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 25px auto;
}
.social-icon {
  margin:0 10px;
  width: 30px!important;
  height: 30px!important;

}
.social-svg-mask {
  fill: black!important;
}
.copyright,
.site-design {
  width:46%;
  margin-top:25px;
  display:inline-block;
  margin-right:2%;
  margin-left:2%;
}
.copyright {
  float:left;
  text-align: left;
}
.site-design {
  float:right;
  text-align: right;
}
@media screen and (min-width: 600px) {
  .book-details {
    width:70%;
    float:left;
    margin-bottom: 50px;
  }
  .book-covers {
    width:25%;
    max-width:115px;
    float:left;
    margin:-65px auto 0;
  }
  .cover {
    width:75%;
    float:none;
    margin:0 auto 15px;
    display:block;
  }
  .buy-now {
    width:90%;
    float:none;
    margin-left:auto;
    margin-right:auto;
    display: block;
  }
  .synopsis {
    clear:both;
    margin-top: 25px;
  }
  .light-pink {
    background-repeat: no-repeat;
    background-size: 110%  900px;
  }
}
@media screen and (min-width: 670px) {
  main {
    margin-top:50px;
  }
  header {
    position: relative;
  }
  .hamburger-icon {
    visibility: collapse;
  }
  nav li {
    width: 25%;
    height: 50px;
    padding-top: 12px;
  }
  #home,
  #about,
  #books,
  #reviews {
    width:70%;
    max-width: 600px;
  }
  .left-arrow {
    left:40px;
    bottom: 70px;
  }
  .right-arrow {
    right:40px;
    bottom: 70px;
  }
}


